<template>

    <h1 v-if="!pokemon"> espere por favor ... </h1>

    <div v-else>
        <h1>¿Quien es este Pokemon ?</h1>
        <!-- TODO: picture -->
        <PokemonPicture :pokemonId="pokemon.id" :showPokemon="showPokemon"/>

        <!-- TODO: opciones -->
        <PokemonOptions 
            :pokemons="pokemonArr" 
            @selection="checkAnswer"    
        /> 

        <template v-if="showAnswer" class="fade-in">
            <h2> {{ messague }} </h2>

            <button @click="newGame">
                nuevo Juego
            </button>
        </template>
    </div>
</template>

<script>
import PokemonPicture from '@/components/PokemonPicture.vue';
import PokemonOptions from '@/components/PokemonOptions.vue';
import getPokemonsOptions from '@/helpers/getPokemonOtions.js';


export default {
    
    components: {
        PokemonPicture,
        PokemonOptions
    },
    data(){
        return {
            pokemonArr: [],
            pokemon: null,
            showPokemon: false,
            showAnswer:false,
            messague: ''
        }
    },
    methods: {
        async mixPokemonArr(){
            this.pokemonArr = await getPokemonsOptions();

            const rdInt = Math.floor(Math.random() * 4 );

            this.pokemon = this.pokemonArr[ rdInt ]
        }, 
        checkAnswer( pokemonId ){
            console.log('se hizo click', pokemonId);
            this.showPokemon = true;
            this.showAnswer = true;

            pokemonId === this.pokemon.id
                ? this.messague = `correcto, ${ this.pokemon.name}`
                : this.messague = `Oops era ${ this.pokemon.name }`
        },
        newGame(){
            this.showPokemon = false
            this.showAnswer = false
            this.pokemonArr = []
            this.pokemon = null
            this.mixPokemonArr()
        }
    },
    //hooks ganchos 
    // componentes para el ciclo de vida
    mounted(){
        // console.log('mounted')
        this.mixPokemonArr();
    }
}
</script>