<template>
    <div class="options-container">
        <ul>
            <li v-for="pokemon in pokemons" 
            :key="pokemon.id"
            @click="$emit('selection', pokemon.id)"
            >
                {{ pokemon.name  }}
            </li>
        </ul>
    </div>
</template>

<script>


export default {

    /******* 
     * el $emit se le coloca el $para indicar o separar que este es un metodo 
     * o propiedad propia de vue ya que sin eso podriamos reescribirla facilmente. 
    */
    
    // computed: {
    //     imgSrc(){
    //         return ``;   
    //     }
    // }
    props: {
        pokemons: {
            type: Array,
            requided: true,
            default: []
        }
    }
}
</script>

<style scoped>

    /* Pokemon Options */
    ul {
        list-style-type: none;
    }
    li {
        background-color: white;
        border-radius: 5px;
        border: 1px solid rgba(0, 0, 0, 0.2);
        cursor: pointer;
        margin-bottom: 10px;
        width: 250px;
    }

    li:hover {
        background-color: rgba(0, 0, 0, 0.05);
    }

    .options-container {
        display: flex;
        justify-content: center;
    }
</style>
